.nav {
  display: flex;
  justify-content: space-evenly;
  height: 150px;

  @media (max-width: 600px) {
    min-height: 210px;
    height: auto;
  }
  h1 {
    text-align: center;
    text-transform: uppercase;
    color: #d0b58f;
    font-size: 60px;
    max-width: 90vw;
    margin-top: 70px;
  }

  .lang {
    position: absolute;
    top: 10px;
    background-color: #d0b58f;
    color: #70000e;
    @media (min-width: 600px) {
      right: 20px;
    }

  }

  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url("data:image/svg+xml;utf8,<svg fill='maroon' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 2px;
    border-radius: 2px;
    padding: 7px;
    padding-right: 2rem;
  }
  select:focus-visible {
    outline: none;
  }
}

.content {
  min-height: 750px;
  @media (max-width: 600px) {
    min-height: 61vh;
  }
  .container {
    width: 790px;
    background-color: rgba(255, 255, 255, 0.479);
    border-radius: 10px;
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 20px;
    @media (max-width: 600px) {
      width: 90vw;
    }

    .switch {
      margin-top: 10px;
      button {
        border: none;
        background-color: #d0b58f;
        color: #70000e;
        font-size: 17px;
        font-family: FuturaNDAlt;
        text-transform: uppercase;
        padding: 5px 10px;
        margin-top: 10px;
        cursor: pointer;
      }
      .switcher-left {
        border-radius: 10px 0 0 10px;
      }

      .switcher-right {
        border-radius: 0 10px 10px 0;
      }

      .active {
        background-color: #70000e;
        color:#d0b58f;
      }
    }

    .hidden {
      display: none;
    }

    h3 {
      margin: 10px;
    }
  
    .inputs {
      margin-bottom: 20px;
      width: 100%;
      margin-top: -40px;
    }
  
    .input {
      margin-top: 50px;
      border: none;
      padding: 10px;
      width: 70%;
    }

    .inputDate {
/*       margin-top: 108px; */
    }
  
    .button {
      background-color: #70000e;
      border: none;
      text-decoration: none;
      cursor: pointer;
      color: #d0b58f;
      font-family: FuturaNDAlt;
      text-transform: uppercase;
      font-size: 30px;
      padding: 5px 20px;
    }
  }


  .song-link {
    margin-top: 25px;
  }
  
  .result-text {
    font-size: 40px;
    margin-bottom: 10px;
    max-width: 80vw;
    margin-top: 20px;
    .result {
      color: #70000e;
    }
  }
  
  .hidden {
    display: none;
  }
}

.footer {
/*   position: absolute;
  top: 96%; */
  width: 100%;
  text-align: center;
  padding: 5px 0;
}

.button-link {
  margin-top: 25px;
  color: black;
  text-decoration: underline;
  border: none;
  background: none;
  font-family: 'FuturaNDAlt';
  font-size: 17px;
  cursor: pointer;
}

.link {
  color: black;
}

